import React from 'react';
import { Link } from 'react-router-dom';

function Home() {
	return (
        <div id="home">
            <header>
                <nav>
                    <label className="brand">Jzonn</label>
                    <ul>
                        <Link to="/about">
                            <li>About</li>
                        </Link>
                        <Link to="/shop">
                            <li>Shop</li>
                        </Link>
                    </ul>
                </nav>
            </header>
            <div>
                <h1>Home</h1>
            </div>
        </div>
    );    
}

export default Home;