import React from 'react';
import { Link } from 'react-router-dom';

function About() {
	return (
        <div>
            <header>
                <nav>
                    <label className="brand">Jzonn</label>
                    <ul>
                        <Link to="/">
                            <li>Home</li>
                        </Link>
                        <Link to="/shop">
                            <li>Shop</li>
                        </Link>
                    </ul>
                </nav>
            </header>
            <h1>About</h1>
        </div>
	);
}

export default About;